body {
  margin: 0;
  font-family:  "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
}
h1{
    font-weight: 300;
}
h1.main_heading-text{
color: black;
padding: 16px;
font-size: 16px;
text-align: left;
font-weight: bold;
margin-bottom: 0; 
padding-bottom: 0;
padding-left: 0;
}
.basic-multi-select {
  display: inline-flex;
  min-width: 150px;
}
.select__control{
  min-width: 150px;
  BORDER: 0px!important;
    border-bottom: 1.2px solid rgba(0, 0, 0, 0.33)!important;
    border-radius: 0px!important;
}
.select__indicator-separator{
  background-color:transparent!important;
}
.basic-multi-select  +  div{
   display: inline-flex;
  width:90px;
  margin-left:  10px;
}
span.MuiListItemText-primary{
  font-size: 14px;
}
.MuiDrawer-docked  .MuiIconButton-root{
  color: white!important;
}
#MenuItems .MuiListItemIcon-root  { 
  color: rgba(255, 255, 255, 0.54);
  min-width: 38px;
}
 div > .active-VERY{
  background: #f5f5f5!important;
  width: 100%;
  color:black!important;
  display: flex;
    padding: 11px 0;
 
}
 .active-VERY .MuiListItemText-root{
  display: inline-block;
}
 .active-VERY span{
font-weight: bold;}
#MenuItems .active-VERY .MuiListItemIcon-root {
  color: #1a8679;padding-left: 13px;
}
#MenuItems .MuiListItem-button a{ display: flex;}
.MuiDrawer-paperAnchorDockedLeft{
  border-right: none!important;
}
.primary-heading{
  
  text-transform: uppercase ;
  background: transparent;
}
.primary-heading span{
  
  font-weight: bold;
}
.inset-heading{
  margin-left: 20px;
}
#MenuItems .MuiListItem-gutters{
  padding-right:  0px!important ;padding-left: 24px;
}

.MuiFormHelperText-root.Mui-error.Mui-required p{
  color: #f44336;
}

@media only screen and (min-width : 950px) {
.MuiDialog-paperWidthSm{
  min-width: 500px;
}

}
@media only screen and (max-width : 950px) {
  #MenuItems .MuiListItem-gutters{
    padding-right:  0px!important ;padding-left: 13px;
  }
.MuiDialogActions-root.MuiDialogActions-spacing{
  flex-direction: column; 
 } 
}

#MenuItems .MuiPaper-root{
  background-color: transparent;
}
#MenuItems .primary-heading{
  color: white;
}

#MenuItems   .MuiExpansionPanelDetails-root {
  display: flex;
  padding: 8px 0px 0px;
}

#MenuItems  .inset-heading {
  margin-left: 5px;
}
#MenuItems .MuiList-padding{
  width: 260px;
}

#MenuItems .MuiExpansionPanelSummary-content.Mui-expanded {
  margin: 8px 0;
}
#MenuItems .MuiExpansionPanel-root.Mui-expanded {
  margin: 8px 0;
}
#MenuItems .MuiExpansionPanelSummary-root.Mui-expanded {
  min-height: 36px;
}